import React from "react";

const TopeventSekleton = () => {
  const List = (e, x, y) => {
    return (
      <div
        className={`sk_line sk_line_${e} sk_line_height_${x} ${y ? y : ""}`}
      ></div>
    );
  };

  return [1, 2, 3, 4].map(() => {
    return (
      <tr>
        <td>
          <div>
            <List e={50} x={16} />
          </div>
        </td>
        <td>
          <div className="d-flex flex-column w-100">
            <div className="d-flex m_grid_author w-100">
              <div className="sk_line sk_img m-0 me-3"></div>
              <div className="w-50">
                <h3 className="sk_line sk_line_height_16 sk_line_90 m-0 mb-3"></h3>
                <p className="sk_line sk_line_height_10 sk_line_60 m-0"></p>
              </div>
            </div>
          </div>
        </td>
        <td>
          <div>
            {" "}
            <List e={30} x={16} />
          </div>
        </td>
        <td>
          <div>
            <List e={30} x={16} />
          </div>
        </td>
        <td>
          <div className="d-flex">
            <List e={20} x={16} y={"me-2"} />
            <List e={20} x={16} y={"me-2"} />
            <List e={20} x={16} y={"me-2"} />
            <List e={20} x={16} />
          </div>
        </td>
        <td>
          <div>
            <List e={100} x={16} />
          </div>
        </td>
      </tr>
    );
  });
};

export default React.memo(TopeventSekleton);
